import StoreStage from '@/components/main/StoreStage.vue';
export default {
  components: {
    StoreStage: StoreStage
  },
  data: function data() {
    return {
      stageConfig: {
        type: 'panel',
        title: '我的仪表板',
        tools: {
          build: {
            show: true
          },
          copy: {
            show: true
          },
          rename: {
            show: true
          },
          delete: {
            show: true
          },
          sort: {
            show: true
          },
          view: {
            show: true
          }
        },
        files: [],
        style: {
          title: {
            backgroundColor: 'rgb(207, 157, 150)'
          }
        }
      }
    };
  },
  mounted: function mounted() {
    var _this = this;

    this.$API.getPanelList(function (message) {
      if (message.success) {
        _this.stageConfig.files = message.info;
      }
    });
  }
};